<template lang="html">
  <div class="_full_router addressBody">
    <custHeader :title="title"></custHeader>

    <ul>
      <li class="addressLi" v-for="(item,index) in addressData">
        <div class="li-header">
          <span>{{ item.recipients }}</span>
          <span style="float:right">{{ item.recipientsMb  }}</span>
        </div>
        <div class="li-content">
          <span v-tranArea:province="{areaNum:item.province }"></span>
          <span v-tranArea:city="{areaNum:item.city,topNum:item.province }"></span>
          <span v-tranArea:area="{areaNum:item.county,topNum:item.city }"></span>
          {{ item.address }}
        </div>
        <div class="li-footer">

          <mt-button  type="primary" size="small" style="margin-top:13px;" class="" @click="editAddress(item.sno)">编辑</mt-button>
          <mt-button  type="primary" size="small"  class="" @click="removeAddress(item.sno)">删除</mt-button>

          <!-- <input type="radio" name="" value="">默认地址</input> -->
          <div class="addressRadio" style="display: inline-block;float: right;">
            <mt-radio
              v-model="isDefault"
              :options='[{label:"默认地址",value:item.sno}]'
            >
            </mt-radio>
          </div>
        </div>
      </li>
    </ul>
    <mt-button  type="primary" size="large"  class="addAddress" @click="addAddress">添加新地址</mt-button>

    <mt-popup
      v-model="addVis"
      position="right"
      style="width:100%"
      >
      <div class="addBody">
        <mt-header title="添加新地址" class="cust-header" style="background:#ef4f4f" >
          <mt-button icon="back" @click="addBack" slot="left">返回</mt-button>
          <mt-button @click="saveAddress" slot="right">保存</mt-button>
        </mt-header>
        <mt-field label="*收货人:" v-model="addForm.addRecipients" v-validtype="rules.addRecipients" :state="rules.addRecipients.state"></mt-field>
        <mt-field label="*收货人电话:" v-model="addForm.addmb" v-validtype="rules.addmb" :state="rules.addmb.state"></mt-field>
        <mt-field label="*省/直辖市:" placeholder="请选择省/直辖市" v-model="addForm.province.name" v-lfoucs="selectTypeVisibleShow3" v-areas="areas.province" ></mt-field>
        <mt-field label="*市:" placeholder="请输入市" v-model="addForm.city.name" v-lfoucs="selectTypeVisibleShow4" v-areas="areas.city" ></mt-field>
        <mt-field label="*县/区:" placeholder="请输入县/区" v-model="addForm.county.name" v-lfoucs="selectTypeVisibleShow5" v-areas="areas.area"></mt-field>
        <mt-field label="*收货地址:" v-model="addForm.addAddress" v-validtype="rules.addAddress" :state="rules.addAddress.state"></mt-field>

      </div>

    </mt-popup>

    <mt-popup
      v-model="editVis"
      position="right"
      style="width:100%"
      >
      <div class="editBody">
        <mt-header title="编辑地址" class="cust-header" style="background:#ef4f4f" >
          <mt-button icon="back" @click="editBack" slot="left">返回</mt-button>
          <mt-button @click="saveEditAddress" slot="right">保存</mt-button>
        </mt-header>
        <mt-field label="*收货人:" v-model="editForm.editRecipients" v-validtype="rules.editRecipients" :state="rules.editRecipients.state"></mt-field>
        <mt-field label="*收货人电话:" v-model="editForm.editmb" v-validtype="rules.editmb" :state="rules.editmb.state"></mt-field>
        <mt-field label="*省/直辖市:" placeholder="请选择省/直辖市" v-model="editForm.province.name" v-lfoucs="selectTypeVisibleShow3" v-areas="areas.province" ></mt-field>
        <mt-field label="*市:" placeholder="请输入市" v-model="editForm.city.name" v-lfoucs="selectTypeVisibleShow4" v-areas="areas.city" ></mt-field>
        <mt-field label="*县/区:" placeholder="请输入县/区" v-model="editForm.county.name" v-lfoucs="selectTypeVisibleShow5" v-areas="areas.area"></mt-field>
        <mt-field label="*收货地址:" v-model="editForm.editAddress" v-validtype="rules.editAddress" :state="rules.editAddress.state"></mt-field>
      </div>

    </mt-popup>



    <mt-popup
      v-model="selectTypeVisible3"
      style="width:80%"
      pop-transtion="popop-fade"
      class="select-dict"
    >
      <mt-picker :slots="selectTypeSlots3"  valueKey="name" @change="selectTypeChange3" :visible-item-count="7"></mt-picker>
      <div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
        <button class="btn" @click="sureSelectType">确认</button>
        </div>
    </mt-popup>

    <mt-popup
      v-model="selectTypeVisible4"
      style="width:80%"
      pop-transtion="popop-fade"
      class="select-dict"
    >
      <mt-picker :slots="selectTypeSlots4"  valueKey="name" @change="selectTypeChange4" :visible-item-count="7"></mt-picker>
      <div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
        <button class="btn" @click="sureSelectType">确认</button>
        </div>
    </mt-popup>

    <mt-popup
      v-model="selectTypeVisible5"
      style="width:80%"
      pop-transtion="popop-fade"
      class="select-dict"
    >
      <mt-picker :slots="selectTypeSlots5"  valueKey="name" @change="selectTypeChange5" :visible-item-count="7"></mt-picker>
      <div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
        <button class="btn" @click="sureSelectType">确认</button>
        </div>
    </mt-popup>


  </div>
</template>

<script>
import custHeader from  'components/custHeader'
import {Radio,MessageBox} from 'mint-ui'
Vue.component(Radio.name,Radio)
export default {
  data(){
    return {
      title:'收货地址管理',
      addVis:false,
      editVis:false,
      isDefault:'1',
      rules:{
				addRecipients:{str:"required",state:""},
				addmb:{str:"required,mobile",state:""},
				// idName:{str:"required",state:""},
        addAddress:{str:"required",state:""},
        editRecipients:{str:"required",state:""},
        editmb:{str:"required",state:""},
				editAddress:{str:"required",state:""}
      },
      // addressData:[{province:"浙江省",city:'杭州市',county:'余杭区',recipients:'张春旭',recipientsMb:'15957199647',address:'五常街道'}],
      addressData:[],
      selectTypeVisible3:false,
  		selectTypeSlots3:[{
          flex:1,
          values:[],
  		}],
  		selectTypeVisible4:false,
  		selectTypeSlots4:[{
          flex:1,
          values:[],
  		}],

  		selectTypeVisible5:false,
  		selectTypeSlots5:[{
          flex:1,
          values:[],
  		}],
      areas:{
  			province:{
  				name:"province",
  				dataKey:"selectTypeSlots3"
  			},
  			city:{
  				name:"city",
  				dataKey:"selectTypeSlots4"
  			},
  			area:{
  				name:"area",
  				dataKey:"selectTypeSlots5"
  			}
  		},
      addForm:{
        addRecipients:'',
        addmb:'',
        addAddress:'',
        province:{name:"",value:""},
    		city:{name:"",value:""},
    		county:{name:"",value:""},
      },
      editForm:{
        sno:'',
        editRecipients:'',
        editmb:'',
        editAddress:'',
        province:{name:"",value:""},
    		city:{name:"",value:""},
    		county:{name:"",value:""},
      }
    }
  },
  methods:{
    addAddress:function(){
      this.addVis = true;
    },
    selectTypeVisibleShow3:function(){
			this.selectTypeVisible3=true;
		},
		selectTypeVisibleShow4:function(){
			this.selectTypeVisible4=true;
		},
		selectTypeVisibleShow5:function(){
			this.selectTypeVisible5=true;
		},
    selectTypeChange3:function(picker,value){
			console.log(value);
			if(value[0]){
        if(this.addVis){
          this.addForm.province.name=value[0].name;
  				this.addForm.province.value=value[0].value;
  				window.areaCity("city",value[0].value).then(data=>{
  					var items=[];
  					 data.forEach(item=>{
                        items.push({value:item.ddItem,name:item.ddItemName});
                      })
  					 this.selectTypeSlots4=[{
  				        flex:1,
  				        values:items,
  						}];
  				})
        }else{
          this.editForm.province.name=value[0].name;
  				this.editForm.province.value=value[0].value;
  				window.areaCity("city",value[0].value).then(data=>{
  					var items=[];
  					 data.forEach(item=>{
                        items.push({value:item.ddItem,name:item.ddItemName});
                      })
  					 this.selectTypeSlots4=[{
  				        flex:1,
  				        values:items,
  						}];
  				})
        }

			}
		},
		selectTypeChange4:function(picker,value){
			console.log(value);
			if(value[0]){
        if(this.addVis){
          this.addForm.city.name=value[0].name;
  				this.addForm.city.value=value[0].value;
  				window.areaCity("area",value[0].value).then(data=>{
  					console.log(data);
  					var items=[];
  					 data.forEach(item=>{
                        items.push({value:item.ddItem,name:item.ddItemName});
                      })
  					 this.selectTypeSlots5=[{
  				        flex:1,
  				        values:items,
  						}];
  				})
        }else{
          this.editForm.city.name=value[0].name;
  				this.editForm.city.value=value[0].value;
  				window.areaCity("area",value[0].value).then(data=>{
  					console.log(data);
  					var items=[];
  					 data.forEach(item=>{
                        items.push({value:item.ddItem,name:item.ddItemName});
                      })
  					 this.selectTypeSlots5=[{
  				        flex:1,
  				        values:items,
  						}];
  				})
        }

			}
		},
		selectTypeChange5:function(picker,value){
			console.log(value);
			if(value[0]){
        if(this.addVis){
          this.addForm.county.name=value[0].name;
  				this.addForm.county.value=value[0].value;
        }else{
          this.editForm.county.name=value[0].name;
  				this.editForm.county.value=value[0].value;
        }

			}
		},
    dissSelectType:function(){
			this.currency='';
			this.selectTypeVisible3=false;
			this.selectTypeVisible4=false;
			this.selectTypeVisible5=false;
		},
    sureSelectType:function(){
			// if(!this.currency){
		  //       this.currency = this.selectTypeSlots[0].values[0].name;
		  //     }
		      this.selectTypeVisible3 = false
		      this.selectTypeVisible4 = false
		      this.selectTypeVisible5 = false
		},
    addBack:function(){
      this.addVis = false
    },

    saveAddress:function(){
      var sendData = {
        province:this.addForm.province.value,
        city:this.addForm.city.value,
        address:this.addForm.addAddress,
        county:this.addForm.county.value,
        recipients:this.addForm.addRecipients,
        recipientsMb:this.addForm.addmb,
      }



      console.log(this.rules.addmb.state != 'success','addmb')
      console.log(this.rules.addRecipients.state != 'success','addRecipient')
      console.log(this.rules.addAddress.state != 'success','addAddress')
      console.log(this.addForm.province.value == '')
      console.log(this.addForm.city.value == '')
      console.log(this.addForm.county.value == '')
      console.log(this.rules.addmb.state != 'success' && this.rules.addRecipients.state != 'success' && this.rules.addAddress.state != 'success')
      if(this.rules.addmb.state != 'success' || this.rules.addRecipients.state != 'success' || this.rules.addAddress.state != 'success' 
          || this.addForm.province.value == '' || this.addForm.city.value == '' || this.addForm.county.value == ''){
        MessageBox.alert("请将信息填写完整").then(action => {
          window.$(".v-modal").hide();
        })

      }else{
        axiosUtil.upCltShopAddressIns(sendData).then(data => {
          var data = data.data;
          if(data.errorcode == 0){
            MessageBox.alert("添加成功").then(action => {
              // router.push("/personal/address")
              window.location.reload()
            })
          }else{
            MessageBox.alert("提示",data.errormsg).then(action => {
              window.$(".v-modal").hide();
            })
          }

        })
      }

    },

    editBack:function(){
      this.editVis = false
    },
    editAddress:function(sno){
      this.editVis = true;
      var editData;
      for (var i = 0; i < this.addressData.length; i++) {
        if(this.addressData[i]["sno"] == sno){
          editData = this.addressData[i];
          break;
        }
      }
      this.editForm.sno = sno;
      this.editForm.editRecipients = editData.recipients
      this.rules.editRecipients.state = this.editForm.editRecipients?'success':''

      this.editForm.editmb = editData.recipientsMb
      this.rules.editmb.state = this.editForm.editmb?'success':''
      
      this.editForm.editAddress = editData.address
      this.rules.editAddress.state = this.editForm.editAddress?'success':''
      
      var self = this;
      window.toArea("province",editData.province).then(data => {
        self.editForm.province.name = data
      })
      self.editForm.province.value = editData.province
      window.toArea("city", editData.city , editData.province).then(data => {
          this.editForm.city.name = data
      })
      self.editForm.city.value = editData.city
      window.toArea("area",editData.county,editData.city).then(data =>{
        this.editForm.county.name = data;
      })
      self.editForm.county.value = editData.county
    },
    saveEditAddress:function(){
      var sendData = {
        sno:this.editForm.sno,
        province:this.editForm.province.value,
        city:this.editForm.city.value,
        county:this.editForm.county.value,
        address:this.editForm.editAddress,
        recipients:this.editForm.editRecipients,
        recipientsMb:this.editForm.editmb
      }
      
      if(this.rules.editmb.state != 'success' || this.rules.editRecipients.state != 'success' || this.rules.editAddress.state != 'success' ){
        MessageBox.alert("请将信息填写完整").then(action => {
          window.$(".v-modal").hide();
        })

      }else{
        axiosUtil.upCltShopAddressUpd(sendData).then(data => {
          var data = data.data;
          if(data.errorcode == 0){
            MessageBox.alert("修改成功").then(action => {
              // router.push("/personal/address")
              window.location.reload()
            })
          }else{
            MessageBox.alert("提示",data.errormsg).then(action => {
              window.$(".v-modal").hide();
            })

          }
        })
      }


    },
    removeAddress:function(sno){
      MessageBox.confirm("确定删除么").then(action => {
        axiosUtil.upCltShopAddressDel({sno:sno}).then(data => {
          var data = data.data;
          if(data.errorcode == 0){
            MessageBox.alert("删除成功").then(action => {
              // router.push("/personal/address")
              window.location.reload()
            })
          }else{
            MessageBox("提示",data.errormsg)
          }
        })
      })
    },
    compare:function(property){
      return function(a,b){
        var v1 = a[property];
        var v2 = b[property];
        return v1 - v2;
      }
    }
  },
  created:function(){
    var self = this;
    axiosUtil.upCltShopAddressQry().then(data => {
      var data = data.data;
      self.addressData = data.data;
      self.addressData.sort(this.compare("usedDate"))
      self.isDefault = self.addressData[0].sno
    });

  },
  components:{
    custHeader
  }
}
</script>

<style lang="css" scoped>
.addressBody{
  background-color: #eee;
}

.addressLi{
  background-color: #fff;
  padding: 10px;
  margin-top: 20px;
}
.li-header{
  padding-bottom: 10px;
}
.li-content{
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
}
.li-footer{
  /*margin-top: 10px;*/
}

.addAddress{
  position: fixed;
  bottom: 0;
  z-index: 3;
}


.addBody,.editBody{
  height: 100vh;
}


.select-dict{
	font-size: 14px;
}

	.select-dict button{
		    width: 50%;
    float: left;
    height: 28px;
    border: none;
    background: #fff;
    border-top: 1px solid #ccc;
	}
	.select-dict button:last-child{
		border-left: 1px solid #ccc;
	}
	.select-dict-btns{
		position: absolute;
		bottom: 0;
		bottom: -28px;
    width: 100%;
	}














</style>



